import { ActionButton } from '@popup/shared/components/ActionButton';
import { usePremiumData } from '@popup/shared/hooks/usePremiumData';

type ExitPopUpProps = {
  onClose: () => void;
};

export function ExitPopUp({ onClose }: ExitPopUpProps): JSX.Element {
  const { handleLogout } = usePremiumData();

  return (
    <>
      <h3>      ?</h3>
      <ActionButton onClick={handleLogout}></ActionButton>
      <ActionButton onClick={onClose}></ActionButton>
    </>
  );
}
